import React, { useMemo } from 'react'
import Sizing2 from './contact/large.js'
import "./Images";
import Images from './Images'
import HeartApp from './Heart'

import {Grid, Row} from 'react-flexbox-grid'
//Silo Pieces to where SOLD has no Description
const Gallery2 = () => {
    const art1 = useMemo(() =>
    [
        
        {
            id:1,
            image:Images.hope,
            name:'The Hope Of A Generation',
            price:'$350'
        },
        {
            id:2,
            image:Images.streetlife,
            name:'Street Life',
            price:'$400'
        },
        {
            id:3,
            image:Images.spiritman,
            name:'Spirit Man',
            price:'$400'
        },
       
        {
            id:4,
            image:Images.helping,
            name:'Overcome With Grief',
            price:'$400'
        },
       
       
        {
            id:5,
            image:Images.waitingroom,
            name:'The Waiting Room',
            price:'$350'
        },
        {
            id:6,
            image:Images.confidentwoman,
            name:'The Confident Woman',
            price:'$400'
        },
        {
            id:7,
            image:Images.ruby,
            name:'Ruby',
            price:'$350'
          },
    ],
    []
    )
return (
    <div>
    <Grid>
        <Row>
            
                {art1.map((val, key) => {
                  return (
                    <div key={key}>
                    <img className='pic1' src={val.image} alt="" />
                    <h3 className='titles'>{val.name}</h3>
                    <HeartApp />
                    <h4>{val.price}</h4>
                    <Sizing2 />
                    </div>
                  )
                })}
              
                
        </Row>
    </Grid>
    </div>

    )
}
export default Gallery2